body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.chronometer {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.chronometer__center {
  z-index: 2;
  background-color: #f6a606;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.chronometer__seconds {
  transform-origin: 50% 0;
  z-index: 1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-chronometer;
  position: absolute;
  top: 50%;
  left: 48.26%;
}

.chronometer__minutes {
  transform-origin: 50% 0;
  z-index: 1;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-chronometer;
  position: absolute;
  top: 50%;
  left: 48.26%;
}

@keyframes rotate-chronometer {
  from {
    transform: rotate(-180deg);
  }

  to {
    transform: rotate(180deg);
  }
}

.chronometer--speed-up .chronometer__seconds {
  animation-duration: 10s;
}

.chronometer--speed-up .chronometer__minutes {
  animation-duration: 600s;
}
